.banner {
    color: white;
    object-fit: contain;
    height: 55vh;
}

.banner-content {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner-title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner-desc {
    width: 45rem;
    height: 80px;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
}

.banner-btn {
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    padding-bottom: 0.5rem;
}

.banner-btn:hover {
    color: black;
    background-color: rgba(255, 255, 255, 0.7);
    transition: all 0.2s;
}

.banner-fade {
    height: 55%;
    background: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111);
}